@import url('https://fonts.googleapis.com/css?family=Acme');

.Question-root {
    font-family: 'Acme', sans-serif;
    background-color: #fff;
    border-radius: 1em;
    padding: 1em;
}

.Question-question {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.Question-text {
    font-size: 200%;
    width: 100%;
}

.Question-image {
    max-width: 30%;
    -ms-flex-item-align: center;
        align-self: center
}

.Question-respondent {
    clear: both;
    margin: 5em 0;
}

.Question-choices ul {
    list-style: none;
    padding: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.Question-choices ul li {
    -ms-flex: 1 1;
        flex: 1 1;
}

.Question-choice {
    display: block;
    text-align: center;
    font-size: 350%;
    text-decoration: none;
    color: #ff6633;
    border: solid 4px #ff6633;
    border-radius: 12px;
    padding: 0.5em;
    margin: 0.1em;
}

.Question-choice:hover {
    background-color: #ff6633;
    color: #fff;
}

.Question-thanks {
    font-size: 350%;
    color: #ff6633;
    text-align: center;
}

.Question-scan {
    font-size: 200%;
    color: #ff6633;
    text-align: center;
}



